<template>
  <div id="talk">
    <v-container>
      <div class="pt-md-8"></div>
      <v-btn text plain depressed @click="goback()">
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-card flat>
        <v-container class="chat">
          <div
            v-for="item in contentList"
            :key="item.name"
            class="user"
            :class="leftOrRight(item)"
          >
            <v-avatar class="avatar" size="64">
              <v-img :src="userImg(item)" />
            </v-avatar>
            <div class="content">
              <div class="name">{{ userName(item) }}</div>
              <div class="overall">
                <div class="text">{{ item.Conversation }}</div>
              </div>
              <div class="time">
                {{
                  item.TimeCreate.replace("T", " ").substring(
                    0,
                    item.TimeCreate.length - 5
                  )
                }}
              </div>
            </div>
          </div>
        </v-container>
        <div class="pt-8"></div>
      </v-card>
    </v-container>
    <v-card class="text-field pa-1">
      <div class="d-flex align-center">
        <v-text-field
          v-model="message"
          background-color="#eceff1"
          placeholder="Aa"
          outlined
          hide-details="auto"
          dense
        ></v-text-field>
        <v-btn class="mr-md-4" text plain depressed fab @click="messageSend()">
          <v-icon x-large> mdi-send-circle-outline </v-icon>
        </v-btn>
      </div>
    </v-card>
  </div>
</template>

<script>
import { serviceContent, serviceSend } from "@/api";

export default {
  name: "App",

  data: () => ({
    contentList: "",
    contentData: "",
    message: "",
  }),

  created() {
    const params = new URLSearchParams();
    params.append("serviceId", this.userId);

    serviceContent(params).then((response) => {
      this.contentList = response.data.datas;
      this.contentData = response.data;
      console.log(response);
    });
  },

  updated() {
    const scrollToBottom = () => {
      const scrollEl = document.getElementById("talk");
      console.log(scrollEl);
      scrollEl.scrollTop =
        document.getElementsByClassName("chat")[0].scrollHeight;
      console.log(document.getElementById("talk").scrollTop);
      console.log(document.getElementsByClassName("chat")[0].scrollHeight);
    };

    scrollToBottom();
  },

  computed: {
    userId() {
      return this.$route.params.id;
    },
  },

  methods: {
    goback() {
      this.$router.go(-1);
    },

    messageSend() {
      const params = new URLSearchParams();
      params.append("serviceId", this.userId);
      params.append("conversation", this.message);

      serviceSend(params).then((response) => {
        console.log(response);
        if (response.data.status_code == "SUCCESS") {
          location.reload();
        } else {
          alert("發送時發生錯誤，請稍後重試");
        }
      });
    },

    userName(item) {
      if (item.ManagerName === "") {
        return item.ConsultantName;
      } else {
        return item.ManagerName;
      }
    },

    userImg(item) {
      if (item.ConsultantName === "") {
        return require("@/assets/user1.jpg");
      } else {
        return require("@/assets/user2.jpg");
      }
    },

    leftOrRight(item) {
      if (item.ConsultantName == "") {
        return "local";
      } else {
        return "remote";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#talk {
  height: 870px;
  overflow-y: scroll;
  overflow-x: hidden;
  white-space: nowrap;
}

@media screen and(max-width: 600px) {
  #talk {
    height: 1300px;
    overflow-y: scroll;
    overflow-x: hidden;
    white-space: nowrap;
  }
}

@media screen and(max-width: 768px) {
  #talk {
    height: 900px;
    overflow-y: scroll;
    overflow-x: hidden;
    white-space: nowrap;
  }
}

@media screen and(max-width: 1024px) {
  #talk {
    height: 870px;
    overflow-y: scroll;
    overflow-x: hidden;
    white-space: nowrap;
  }
}

::-webkit-scrollbar {
  width: 0 !important;
}

::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
}

.user {
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;
  .avatar {
    align-self: center;
    flex-shrink: 0;
  }
  .pic {
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      vertical-align: middle;
    }
  }
  .text {
    background-color: #aaa;
    padding: 16px;
    border-radius: 10px;
    position: relative;
    max-width: 350px;
  }
}

.remote {
  .content {
    display: block;
    margin-right: 20px;
    margin-left: 30px;
    .name {
      color: #333;
    }
    .overall {
      display: flex;
      .text {
        color: #eee;
        background-color: #4179f1;
      }
    }
  }
}

.local {
  justify-content: flex-end;
  .content {
    display: block;
    order: -1;
    margin-right: 20px;
    margin-left: 80px;
    .name {
      color: #333;
      text-align: end;
    }
    .time {
      text-align: end;
    }
    .overall {
      display: flex;
      justify-content: flex-end;
      .text {
        background-color: #eceff1;
        color: #333;
      }
    }
  }
}

.remote,
.local {
  .text {
    font-weight: 700;
    box-shadow: 0 0 10px #eceff1;
  }
}

.text-field {
  width: 100vw;
  background-color: white;
  //   position: -webkit-sticky; /* Safari */
  //   position: sticky;
  position: fixed;
  bottom: 0%;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  -webkit-border-radius: 5px;
}

::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: #909399;
  border-radius: 5px;
  -webkit-border-radius: 5px;
}

::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: #909399;
  border-radius: 5px;
  -webkit-border-radius: 5px;
}
</style>